<template>
    <v-flex class md8 offset-md2>
        <form id="login-form">
            <v-flex id="login-input-container">
                <v-text-field
                    v-model="id"
                    type="text"
                    :error-messages="idErrors"
                    label="아이디"
                    required
                    @input="$v.id.$touch()"
                    @blur="$v.id.$touch()"
                ></v-text-field>
                <v-text-field
                    v-model="password"
                    type="password"
                    :error-messages="passwordErrors"
                    :counter="20"
                    label="비밀번호"
                    required
                    @input="$v.password.$touch()"
                    @blur="$v.password.$touch()"
                ></v-text-field>
                <v-text-field
                    v-model="passwordConfirm"
                    type="password"
                    :error-messages="passwordConfirmErrors"
                    :counter="20"
                    label="비밀번호 확인"
                    required
                    @input="$v.passwordConfirm.$touch()"
                    @blur="$v.passwordConfirm.$touch()"
                ></v-text-field>
            </v-flex>
            <v-flex id="login-btn-container">
                <v-btn block color="primary" dark @click="submit">제출하기</v-btn>
                <v-btn block color="primary" dark @click="clear">다시입력</v-btn>
            </v-flex>
        </form>
    </v-flex>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
    required,
    minLength,
    maxLength
} from "vuelidate/lib/validators";

import axios from "axios";
import {createNamespacedHelpers} from 'vuex';
const  { mapState, mapActions } = createNamespacedHelpers('ui/member');

export default {
    name: "join",
    mixins: [validationMixin],

    // $v 객체에 해당한다.
    // 필요한 validation 정보 정의
    validations: {
        id: { 
            required,
            minLength: minLength(8),
            maxLength: maxLength(15)
        },
        password: {
            required,
            minLength: minLength(8),
            maxLength: maxLength(20)
        },
        passwordConfirm: {
            required,
            minLength: minLength(8),
            maxLength: maxLength(20)
        }
    },

    data: () => ({
        id: "",
        password: "",
        passwordConfirm: ""
    }),
    created() {},
    mounted() {},
    computed: {
        idErrors() {
            const errors = [];
            if (!this.$v.id.$dirty) return errors;
            !this.$v.id.required &&
                errors.push("아이디를 입력해주세요.");
            if (!this.$v.id.minLength || !this.$v.id.maxLength) {
                errors.push("아이디 입력은 8~15자리 입니다.");
            }
            return errors;
        },
        passwordErrors() {
            const errors = [];
            if (!this.$v.password.$dirty) return errors;
            if (!this.$v.password.minLength || !this.$v.password.maxLength) {
                errors.push("비밀번호는 8~20자리 입니다.");
            }

            !this.$v.password.required &&
                errors.push("비밀번호를 입력해주세요.");
            return errors;
        },
        passwordConfirmErrors() {
            const errors = [];
            if (!this.$v.passwordConfirm.$dirty) return errors;
            if (
                !this.$v.passwordConfirm.minLength ||
                !this.$v.passwordConfirm.maxLength
            ) {
                errors.push("비밀번호 확인은 8~20자리 입니다.");
            }

            if (
                !this.$v.passwordConfirm.required ||
                this.passwordConfirm !== this.password
            ) {
                errors.push("비밀번호를 확인해주세요.");
            }
            return errors;
        }
    },

    methods: {
        ...mapActions(['gotoLogin']),
        submit() {
            this.$v.$touch();

            if (
                !this.idErrors.length &&
                !this.passwordErrors.length &&
                !this.passwordConfirmErrors.length
            ) {
                axios
                    .post(`http://localhost:5001/api/member`, {
                        headers: {
                            "Content-type": "application/x-www-form-urlencoded"
                        },
                        id: this.id,
                        password: this.password
                    })
                    .then(response => {
                        console.log(response);
                        if(response.data === 'ok') {
                            alert('회원가입에 성공하였습니다.');
                            this.gotoLogin();
                        }else if(response.data === 'fail') {
                            alert('이미 존재하거나 사용할수 없는 아이디입니다.');
                        }
                    })
                    .catch(error => {
                        console.log(error);
                        alert('회원가입 중 오류가 발생했습니다.\n다시 시도해주세요.');
                    });
            }
        },
        clear() {
            this.$v.$reset();
            this.id = "";
            this.password = "";
            this.passwordConfirm = "";
        }
    }
};
</script>

<style>
#login-btn-container {
    margin-top: 7vh;
}
</style>
