import Vue from "vue";
import store from "./store";
import Router from "vue-router";
import axios from "axios";

import Home from "./views/Home.vue";
import Login from "./views/Login.vue";
import Join from "./views/Join.vue";

Vue.use(Router);

function getCurrentUser() {
    // ???
    return axios
            .get(`http://localhost:5001/api/member/check`, { withCredentials: true })
            .then(response => {
                return response;
            })
            .catch(error => {
                console.log(error);
            });
}

const preventAccessWithoutLogin = async function(to, from, next) {
    const uiState = store.state.ui; 
    const memberState = uiState.member;
    const userSession = getCurrentUser();
    
    if (userSession) {
      uiState.titles[1].title = "로그아웃";
      uiState.titles[1].goto = "/logout";

      next();
    } else {
        if (to.path !== "/login" && to.path !== "/join") {
            alert("로그인 후 이용해주세요.");
            uiState.titles[1].title = "로그인";
            uiState.titles[1].goto = "/login";
            memberState.loginStatus = false;
            memberState.userInfo = undefined;

            next({path: '/login'});
        }
    }
};

const doLogout = (to, from, next) => {
    const uiState = store.state.ui; 
    const memberState = uiState.member;

    axios
      .get(`http://localhost:5001/api/member/logout`, 
      {
        withCredentials : true
      })
      .then(response => {
          console.log(response);

          if(response.data){
              uiState.titles[1].title = "로그인";
              uiState.titles[1].goto = "/login";
              memberState.loginStatus = false;
              memberState.userInfo = undefined;

              next({name: 'login'});
          }
      })
      .catch(error => {
          console.log(error);
      });
};

export default new Router({
  mode : 'history',
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
      beforeEnter : preventAccessWithoutLogin
    },
    {
      path: "/login",
      name: "login",
      component: Login,
    },
    {
      path: "/logout",
      name: "logout",
      beforeEnter : doLogout,
    },
    {
      path: "/join",
      name: "join",
      component: Join
    }
  ]
});
