<template>
    <v-flex class md8 offset-md2 ref="loadingContainer">
        <form id="login-form">
            <v-flex id="login-input-container">
                <v-text-field
                    v-model="id"
                    type="text"
                    :error-messages="idErrors"
                    label="아이디"
                    required
                    @input="$v.id.$touch()"
                    @blur="$v.id.$touch()"
                ></v-text-field>
                <v-text-field
                    v-model="password"
                    type="password"
                    :error-messages="passwordErrors"
                    :counter="20"
                    label="비밀번호"
                    required
                    @input="$v.password.$touch()"
                    @blur="$v.password.$touch()"
                ></v-text-field>
            </v-flex>
            <v-flex id="login-btn-container">
                <v-checkbox
                    v-model="checkbox"
                    label="아이디 저장"
                    @change="$v.checkbox.$touch()"
                    @blur="$v.checkbox.$touch()"
                ></v-checkbox>
                <v-btn block color="primary" dark @click="submit">로그인</v-btn>
                <v-btn block color="primary" dark @click="clear">재입력</v-btn>
                <v-btn block color="primary" dark @click="gotoJoin">회원가입</v-btn>
            </v-flex>
        </form>
    </v-flex>
</template>

<script>
import axios from 'axios';
import { validationMixin } from "vuelidate";
import {required, minLength, maxLength} from "vuelidate/lib/validators";
import {createNamespacedHelpers} from 'vuex';
const { mapActions } = createNamespacedHelpers('ui/member');

export default {
    name: "login",
    mixins: [validationMixin],

    // $v 객체에 해당한다.
    // 필요한 validation 정보 정의
    validations: {
        id: { 
            required,
            minLength: minLength(8),
            maxLength: maxLength(15) 
        },
        password: {
            required,
            minLength: minLength(8),
            maxLength: maxLength(20)
        },
        checkbox: {
            checked(val) {
                return val;
            }
        }
    },

    data: () => ({
        id: "kangkrkr",
        password: "12341234",
        checkbox: false,
    }),
    created() {
        if (this.$cookies.get("loginSaveEmail")) {
            this.id = this.$cookies.get("loginSaveEmail");
        }
    },
    mounted() {},
    computed: {
        idErrors() {
            const errors = [];
            if (!this.$v.id.$dirty) return errors;
            !this.$v.id.required &&
                errors.push("아이디를 입력해주세요.");
            return errors;
        },
        passwordErrors() {
            const errors = [];
            if (!this.$v.password.$dirty) return errors;
            if (!this.$v.password.minLength || !this.$v.password.maxLength) {
                errors.push("비밀번호는 8~20자리 입니다.");
            }

            !this.$v.password.required &&
                errors.push("비밀번호를 입력해주세요.");
            return errors;
        }
    },
    methods: {
        ...mapActions(['gotoJoin', 'gotoMain']),
        submit() {
            this.$v.$touch();

            const loginObj = {
                $v    : this.$v,
                id : this.id,
                password : this.password,
                checkbox : this.checkbox,
                idErrors : this.idErrors,
                passwordErrors : this.passwordErrors
            };

            axios
                .post(`http://localhost:5001/api/member/login`, {
                    headers: {
                        "Content-type": "application/x-www-form-urlencoded"
                    },
                    id: this.id,
                    password: this.password
                })
                .then(response => {
                    if(response.data.status === 'ok') {
                        alert('로그인에 성공하였습니다.');
                        this.gotoMain(response.data);
                        //this.gotoLogin();
                    }else if(response.data.status === 'fail') {
                        alert('로그인에 실패하였습니다.\n입력정보를 다시 확인해주세요.');
                    }
                })
                .catch(error => {
                    console.log(error);
                    alert('로그인 중 오류가 발생했습니다.\n다시 시도해주세요.');
                });
        },
        clear() {
            this.$v.$reset();
            this.id = "";
            this.password = "";
        },
    },
    created() {
        
    },
};
</script>

<style>
#login-btn-container {
    margin-top: 7vh;
}
</style>
